  margin-bottom: 24px;
}
.modal-content .btn-primary:hover {
  cursor: pointer;
  background: #67911b;
  border-color: #67911b;
}

.modal-body-sing-in {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
