body {
  position: relative;
  z-index: 1;
}

.container-main {
  padding-top: 12px;
  padding-bottom: 0;
}

.fade {
  animation-name: fade;
  animation-duration: 1.7s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.detailed-page__wrap-img {
  position: relative;
}
.detailed-page__resize-container {
  position: absolute;
  top: 10px;
  left: 28px;
  z-index: 1;
  background-color: transparent;
}
.detailed-page__resize-container:hover {
  cursor: pointer;
  scale: 1.3;
}
.detailed-page__image {
  width: 100%;
}
.detailed-page__image-small {
  object-fit: cover;
}
.detailed-page__price {
  font-size: 3rem;
  font-weight: 900;
}
.detailed-page__container {
  position: relative;
  height: fit-content;
  width: fit-content;
  margin-bottom: 20px;
  display: inline-block;
  margin-right: 20px;
}
.detailed-page__container .card-img-overlay {
  background-color: white;
  opacity: 0.6;
  border: 1px solid grey;
}
.detailed-page__container .active-img {
  opacity: 0;
}
.detailed-page__info {
  padding-left: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.detailed-page__color-select {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 152px;
  height: 40px;
  border: 2px solid #e8e2d6;
  border-radius: 50px;
}
.detailed-page__color {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #585d61;
}
.detailed-page__size-select {
  display: flex;
  gap: 24px;
}
.detailed-page__print {
  display: flex;
  justify-content: space-evenly;
  color: #b2b2b2;
  padding: 0 10px;
}
.detailed-page__print:hover {
  cursor: pointer;
  color: #45413e;
  scale: 1.1;
}
.detailed-page__print--icon {
  font-size: 2rem;
  padding-right: 20px;
}
.detailed-page__text {
  color: #45413e;
}
.detailed-page__size {
  font-size: 1.5rem;
}
.detailed-page__share {
  display: flex;
  gap: 36px;
}
.detailed-page__share-title {
  color: #b2b2b2;
}
.detailed-page__link {
  color: #b2b2b2;
  text-decoration: none;
}
.detailed-page__link:hover {
  text-decoration: underline;
}

.container-active {
  border: #84bc22 1px solid;
}

.hide-img {
  display: none;
}

.toggle-hidden-text {
  color: #84bc22;
  cursor: pointer;
}

.detailed-page__read-less {
  display: none;
}

.input-group {
  flex-wrap: nowrap;
  align-items: center;
  width: 130px;
  border: 2px solid #e8e2d6;
  border-radius: 20px;
}
.input-group .btn {
  width: fit-content;
  font-size: 1rem;
  font-weight: 800;
  color: #e8e2d6;
}
.input-group .btn:focus {
  box-shadow: none;
}

.input-count {
  border: none;
  display: inline-block;
}
.input-count:focus {
  box-shadow: none;
}

.btn-success {
  background-color: #85bc23;
  border-color: #85bc23;
}
.btn-success:hover {
  background-color: #67911b;
  border-color: #67911b;
}
.btn-success:focus {
  box-shadow: none;
  background-color: #67911b;
  border-color: #67911b;
}

.col-md-12 {
  gap: 20px;
}

.tab {
  padding-bottom: 0;
}

.tab-content {
  padding-right: 0;
  padding-left: 0;
}

.nav {
  justify-content: center;
  border: none;
}
.nav-link {
  padding: 15px 50px;
  font-size: 1.5rem;
  color: #bdc3c7;
  border-color: #262a33;
  transition: none;
  height: 70px;
}
.nav-link .active:focus {
  border-color: #262a33 !important;
}
.nav-link:hover {
  color: #bdc3c7;
  border: none;
}
.nav .active {
  background-color: #262a33 !important;
  color: #fff !important;
  border-radius: 20px 20px 0 0 !important;
}
.nav .active:hover {
  border-color: #262a33 !important;
}
.nav .active:focus {
  border-color: #262a33 !important;
}

.tab-pane {
  column-count: 2;
  column-gap: 50px;
  padding: 68px;
}
@media (max-width: 768px) {
  .tab-pane {
    column-count: 1;
  }
}

.fade {
  transition: none;
  animation-duration: 0s;
}

.show {
  color: white;
  background-color: #262a33;
  border-radius: 0 0 7px 7px;
}

.popular {
  background-color: #f1edea;
  color: #45413e;
}
.popular__line {
  background-color: #999999;
}
.popular__title--heading {
  font-size: 2rem;
  font-weight: 900;
}
.popular .popular__item .card {
  background-color: transparent;
  border: none;
}
.popular .popular__item .card__main {
  background-color: transparent;
}

.biggest-container {
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  cursor: zoom-out;
}
.biggest-container__hide {
  opacity: 0;
  visibility: hidden;
}

.biggest-image {
  width: 40%;
  height: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 992px) {
  .tab {
    display: none;
  }
}
@media (max-width: 768px) {
  .not-found__title--404 {
    font-size: 2rem;
  }
}
@media (max-width: 576px) {
  .row {
    flex-direction: column;
  }
  .col-sm-12 {
    width: 100%;
  }
  .not-found__line {
    display: none;
  }
  .hide-on-small {
    display: none;
  }
  .popular__line {
    display: none;
  }
}
@media print {
  .print-hide {
    display: none;
  }
  .wrap-internal {
    padding: 0;
  }
  .detailed-page__text {
    display: block;
    padding-top: 20px;
  }
}

